import React, { useState, useEffect, useRef } from 'react';
import styled, { keyframes } from 'styled-components'
import { motion } from 'framer-motion';
import { useNavigate } from 'react-router-dom';

// Import nh LeBron James
import lebronMain from '../assets/images/lebron_main.jpg'; // nh nn ln
import lebronSmall from '../assets/images/lebron_small.png'; // nh nh di chuyn

//  KHNG DNG GLITCH ANIMATION V GLITCH EFFECT NA - CC DNG NY S B COMMENT HOC XA 
//   0% {
//     transform: translate(0);
//   }
//   20% {
//     transform: translate(-5px, 5px);
//   }
//   40% {
//     transform: translate(-5px, -5px);
//   }
//   60% {
//     transform: translate(5px, 5px);
//   }
//   80% {
//     transform: translate(5px, -5px);
//   }
//   100% {
//     transform: translate(0);
//   }
// `;

const TrollContainer = styled.div`
  position: relative;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-image: url(${lebronMain}); /* Nn l nh ln ca LeBron */
  background-size: cover;
  background-position: center;
  font-family: 'Comic Sans MS', cursive;
  color: white;
  text-shadow: 4px 4px 8px rgba(0, 0, 0, 0.7);
  background-color: #000; /* Mu nn d phng */
`;

const TrollMessage = styled(motion.h1)`
  font-size: 5rem; /* Rt ln  "h da" */
  text-align: center;
  margin-bottom: 50px;
  line-height: 1.2;
  color: #ff0000; /* Mu  ni bt */
  text-shadow: 5px 5px 10px rgba(0, 0, 0, 0.8);
  z-index: 10;
`;

const MovingLebron = styled(motion.img)`
  position: absolute;
  width: 150px; /* Kch thc nh LeBron nh */
  height: auto;
  border-radius: 50%; /* Trn nh trong You Are The Idiot */
  border: 5px solid yellow; /* Vin vng ni bt */
  box-shadow: 0 0 20px 10px rgba(255, 255, 0, 0.5); /* Hiu ng sng */
  z-index: 10;
  pointer-events: none; /*  khng chn click */
`;

const BackButton = styled(motion.button)`
  background-color: #e63946; /* Mu  */
  color: white;
  border: none;
  padding: 1.5rem 3rem;
  font-size: 1.8rem;
  font-weight: bold;
  border-radius: 50px;
  cursor: pointer;
  box-shadow: 0 8px 15px rgba(0, 0, 0, 0.3);
  z-index: 10;
  transition: all 0.3s ease-in-out;

  &:hover {
    background-color: #c9303c;
    transform: scale(1.05);
  }
  &:active {
    transform: scale(0.95);
  }
`;

function LebronTrollPage() {
  const navigate = useNavigate();
  const [movingLebrons, setMovingLebrons] = useState([]); // Mng  lu tr v tr ca nhiu LeBron nh
  const containerRef = useRef(null); // Ref  ly kch thc container

  useEffect(() => {
    const spawnInterval = setInterval(() => {
      if (containerRef.current) {
        const containerWidth = containerRef.current.offsetWidth;
        const containerHeight = containerRef.current.offsetHeight;

        const newX = Math.random() * (containerWidth - 150); // 150 l chiu rng nh
        const newY = Math.random() * (containerHeight - 150); // 150 l chiu cao nh

        // Thm mt LeBron mi vo mng
        setMovingLebrons(prevLebrons => [
          ...prevLebrons,
          { id: Date.now(), x: newX, y: newY, opacity: 1 } // Thm opacity  c th fade out sau ny
        ]);

        // Gii hn s lng LeBron nh trn mn hnh (v d 30 con)
        setMovingLebrons(prevLebrons => prevLebrons.slice(Math.max(0, prevLebrons.length - 30)));
      }
    }, 100); // Mi 0.1 giy spawn mt LeBron mi

    return () => clearInterval(spawnInterval); // Dn dp interval
  }, []);

  return (
    <TrollContainer ref={containerRef}> {/* Gn ref vo container */}
      {/*  KHNG DNG GLITCH OVERLAY NA - XA DNG NY  */}
      {/* {glitchEffect && <GlitchOverlay />} */}
      <TrollMessage
        initial={{ opacity: 0, scale: 0.5 }}
        animate={{ opacity: 1, scale: 1 }}
        transition={{ duration: 0.5, delay: 0.5 }}
      >
        LeBron James rt tht vng v bn! 
      </TrollMessage>

      {/* Render nhiu nh LeBron nh di chuyn */}
      {movingLebrons.map(lebron => (
        <MovingLebron
          key={lebron.id}
          src={lebronSmall}
          alt="Lebron James tht vng nh"
          initial={{ opacity: 0, x: lebron.x, y: lebron.y, scale: 0.8 }}
          animate={{ opacity: 1, x: lebron.x, y: lebron.y, scale: 1 }}
          transition={{ type: "spring", stiffness: 100, damping: 10, duration: 0.5 }}
        />
      ))}

      <BackButton onClick={() => navigate('/')} whileHover={{ scale: 1.1 }} whileTap={{ scale: 0.9 }}>
        Thot khi s tht vng! 
      </BackButton>
    </TrollContainer>
  );
}

export default LebronTrollPage;