import React from 'react';
import styled from 'styled-components'; // ng qun import styled!
import { useSpring, animated } from 'react-spring';
import doraemonFlying from '../assets/images/doraemon-flying.png'; // Nh ng dn chnh xc nha!

const HeroWrapper = styled.section`
  background: linear-gradient(135deg, #a8dadc, #457b9d); /* Mu tri xanh m mng */
  color: white;
  text-align: center;
  padding: 8rem 2rem;
  position: relative;
  overflow: hidden;
  min-height: 80vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-family: 'Comic Sans MS', cursive;
`;

const HeroContent = styled.div`
  z-index: 10;
  position: relative;
  h2 {
    font-size: 4.5rem;
    margin-bottom: 1.5rem;
    text-shadow: 4px 4px 8px rgba(0, 0, 0, 0.3);
    background: linear-gradient(45deg, #ffde00, #ff8c00); /* Ch vng cam rc r */
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  }
  p {
    font-size: 1.8rem;
    line-height: 1.6;
    max-width: 800px;
    margin: 0 auto 3rem;
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.2);
  }
`;

const CallToActionButton = styled.button`
  background-color: #e63946; /* Mu  ni bt */
  color: white;
  border: none;
  padding: 1.2rem 2.8rem;
  font-size: 1.5rem;
  font-weight: bold;
  border-radius: 50px;
  cursor: pointer;
  transition: all 0.3s ease-in-out;
  box-shadow: 0 8px 15px rgba(0, 0, 0, 0.2);

  &:hover {
    background-color: #c9303c;
    transform: translateY(-5px) scale(1.05); /* Nhn vo l nhn nhy! */
    box-shadow: 0 12px 20px rgba(0, 0, 0, 0.3);
  }
  &:active {
    transform: translateY(0);
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  }
`;

const DoraemonFlyingImage = styled(animated.img)`
  position: absolute;
  bottom: -10%; /* Bt u t di mn hnh */
  left: -20%; /* Bay t ngoi vo */
  width: 250px; /* Kch thc Doraemon */
  z-index: 5;
  animation: bounce 2s infinite alternate; /* Doraemon bay nhn nhy */

  @keyframes bounce {
    0% { transform: translateY(0); }
    100% { transform: translateY(-20px); }
  }
`;

function HeroSection() {
  // Animation Doraemon bay t tri sang phi, xong ri quay li
  const flyingAnimation = useSpring({
    from: { left: '-20%' },
    to: async (next, cancel) => {
      while (true) {
        await next({ left: '100%', config: { duration: 15000, easing: t => t } }); // Bay sang phi
        await next({ left: '-20%', config: { duration: 0 } }); // Reset v tri ngay lp tc
      }
    },
    loop: true, // Lp li v hn
    config: { duration: 15000 }, // Thi gian bay ht mn hnh
  });

  return (
    <HeroWrapper id="home">
      <DoraemonFlyingImage src={doraemonFlying} alt="Doraemon ang bay" style={flyingAnimation} />
      <HeroContent>
        <h2>Cho mng n vi Th gii ca Doraemon! </h2>
        <p>Khm ph nhng cuc phiu lu bt tn cng Doraemon, Nobita v nhng bo bi thn k!</p>
        <CallToActionButton>Khm ph ngay! </CallToActionButton>
      </HeroContent>
    </HeroWrapper>
  );
}

export default HeroSection;