import React, { useState, useRef, useEffect } from 'react';
import styled from 'styled-components';
import { BrowserRouter as Router, Routes, Route } from 'react-router-dom';

//  CC DNG IMPORT COMPONENT  C SA NG DN CHNH XC RI Y! 
import Header from './components/Header';
import HeroSection from './components/HeroSection'; // <--- SA LI THNH TH NY!
import CharacterCard from './components/CharacterCard';
import GadgetAnimation from './components/GadgetAnimation';
import Footer from './components/Footer';
import CharacterDetail from './components/CharacterDetail';
import LebronTrollPage from './components/LebronTrollPage'; // <--- M BO C DNG NY CNG NG!

import charactersData from './data/characters';
import './styles/App.css';
import './styles/fonts.css';

//  CC DNG IMPORT FILE NHC (ng dn ny vn ng nha!) 
import paradise from './assets/audio/Paradise.mp3';
import giacMoThanTien from './assets/audio/GiacMoThanTien.mp3';
import cuVuiThoi from './assets/audio/CuVuiThoi.mp3';
import noiTaThuocVe from './assets/audio/NoiTaThuocVe.mp3';
import sketch from './assets/audio/Sketch.mp3';

// Styled components cho Home Page sections
const AppContainer = styled.div`
  font-family: 'Comic Sans MS', cursive; /* Set font chung cho ton app */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #333;
  overflow-x: hidden; /* m bo khng c scrollbar ngang */
  position: relative; /*  t nt iu khin nhc */
`;

const SectionTitle = styled.h2`
  font-size: 3.5rem;
  color: #34495e;
  text-align: center;
  margin-top: 5rem;
  margin-bottom: 3rem;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.1);
  font-family: 'Comic Sans MS', cursive;
`;

const CharactersGrid = styled.div`
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 2.5rem;
  justify-items: center;
  padding: 0 2rem;
  max-width: 1200px;
  margin: 0 auto;
`;

const MusicControl = styled.div`
  position: fixed;
  bottom: 20px;
  right: 20px;
  background-color: rgba(0, 123, 255, 0.8); /* Mu xanh Doraemon */
  color: white;
  padding: 10px 15px;
  border-radius: 50px;
  display: flex;
  align-items: center;
  gap: 10px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
  z-index: 1000;
  cursor: pointer;
  font-size: 1rem;
  font-weight: bold;
  transition: background-color 0.3s ease;

  &:hover {
    background-color: rgba(0, 100, 200, 0.9);
  }

  button {
    background: none;
    border: none;
    color: white;
    font-size: 1.5rem;
    cursor: pointer;
    padding: 0 5px;
  }
  span {
    white-space: nowrap;
  }
`;

// Component cho trang ch (Home Page)
function HomePage() {
  return (
    <> {/* Fragment  bao bc nhiu element m khng to thm div */}
      <HeroSection />

      <SectionTitle id="characters">Gp g nhng ngi bn ca Doraemon! </SectionTitle>
      <CharactersGrid>
        {charactersData.map(character => (
          <CharacterCard key={character.id} character={character} />
        ))}
      </CharactersGrid>

      <GadgetAnimation />
    </>
  );
}

function App() {
  const audioRef = useRef(null);
  const [currentSongIndex, setCurrentSongIndex] = useState(0);
  const [isPlaying, setIsPlaying] = useState(false);
  const [currentSongName, setCurrentSongName] = useState('');

  // Danh sch cc bi ht
  const playlist = [
    { name: 'Paradise', src: paradise },
    { name: 'Gic M Thn Tin', src: giacMoThanTien },
    { name: 'C Vui Thi', src: cuVuiThoi },
    { name: 'Ni Ta Thuc V', src: noiTaThuocVe },
    { name: 'Sketch', src: sketch },
  ];

  // Hm  pht nhc
  const playMusic = () => {
    if (audioRef.current) {
      audioRef.current.play().then(() => {
        setIsPlaying(true);
        setCurrentSongName(playlist[currentSongIndex].name);
      }).catch(error => {
        console.error("Li khi c gng pht nhc:", error);
        // y l li trnh duyt chn autoplay, thng bo cho ngi dng click  pht
        alert("Trnh duyt chn t ng pht nhc! Vui lng nhn nt Play  nghe nhc nh! ");
        setIsPlaying(false);
      });
    }
  };

  // Hm  tm dng nhc
  const pauseMusic = () => {
    if (audioRef.current) {
      audioRef.current.pause();
      setIsPlaying(false);
    }
  };

  // Hm chuyn bi khi bi ht kt thc
  const handleSongEnd = () => {
    const nextSongIndex = (currentSongIndex + 1) % playlist.length;
    setCurrentSongIndex(nextSongIndex);
  };

  // Effect  cp nht source nhc v t ng pht bi tip theo
  useEffect(() => {
    if (audioRef.current) {
      audioRef.current.src = playlist[currentSongIndex].src;
      setCurrentSongName(playlist[currentSongIndex].name);
      if (isPlaying) {
        playMusic(); // T ng pht bi mi nu ang  trng thi pht
      }
    }
  }, [currentSongIndex, isPlaying]); // Thm isPlaying vo dependency array  m bo playMusic c gi ng lc

  // Effect  bt u pht nhc khi component mount (th autoplay, nhng s b chn)
  useEffect(() => {
    // Khng gi playMusic() trc tip  y na  trnh li autoplay b chn ngay t u.
    // Thay vo , ngi dng s click vo nt Play.
    setCurrentSongName(playlist[currentSongIndex].name); // Set tn bi ht ban u
  }, []); // Ch chy mt ln khi component c render

  return (
    <Router> {/* Bc ton b ng dng trong Router */}
      <AppContainer>
        <Header /> {/* Header hin th trn tt c cc trang */}
        <Routes> {/* nh ngha cc tuyn ng */}
          <Route path="/" element={<HomePage />} /> {/* Trang ch */}
          <Route path="/character/:id" element={<CharacterDetail />} /> {/* Trang chi tit nhn vt, :id l tham s ng */}
          <Route path="/lebron-troll" element={<LebronTrollPage />} /> {/* <--- M BO ROUTE NY CNG NG */}
          {/* Nu mun thm trang "V chng ti" hoc "Bo bi", bn c th thm Route  y */}
          {/* <Route path="/about" element={<AboutPage />} /> */}
          {/* <Route path="/gadgets-list" element={<GadgetListPage />} /> */}
        </Routes>
        <Footer /> {/* Footer hin th trn tt c cc trang */}

        {/* Th audio n  pht nhc */}
        <audio ref={audioRef} onEnded={handleSongEnd} loop={false} /> {/* loop={false}  t chuyn bi, nu mun lp li c playlist th  true */}

        {/* Nt iu khin nhc ni bt */}
        <MusicControl onClick={isPlaying ? pauseMusic : playMusic}>
          <button>
            {isPlaying ? '' : ''}
          </button>
          <span>{currentSongName || 'Nhn  nghe nhc!'}</span>
        </MusicControl>
      </AppContainer>
    </Router>
  );
}

export default App;